import { GraphqlClient } from './../common/GraphqlClient';
import { ManagementTokenProvider } from './ManagementTokenProvider';
import { ManagementClientOptions } from './types';
import {
  CommonMessage,
  UdfDataType,
  UdfTargetType,
  UserDefinedField,
  UserDefinedData
} from '../../types/graphql.v2';
import { setUdf, removeUdf, udf, udv, setUdvBatch } from '../graphqlapi';
import { convertUdv } from '../utils';

/**
 * @name UdfManagementClient
 * @description Udf  User Defined Field Authing  Authing  \`school\` 
 *  https://docs.authing.co/extensibility/user/extend-register-fields.html 
 *
 * 
 *
 * @example
 *
 * 
 * \`\`\`javascript
 * import { ManagementClient } from "authing-js-sdk"
 * const managementClient = new ManagementClient({
 *    userPoolId: "YOUR_USERPOOL_ID",
 *    secret: "YOUR_USERPOOL_SECRET",
 * })
 * managementClient.udf.list // 
 * managementClient.udf.set // 
 * managementClient.udf.remove // 
 * \`\`\`
 *
 * @class UdfManagementClient 
 */
export class UdfManagementClient {
  options: ManagementClientOptions;
  graphqlClient: GraphqlClient;
  tokenProvider: ManagementTokenProvider;

  constructor(
    options: ManagementClientOptions,
    graphqlClient: GraphqlClient,
    tokenProvider: ManagementTokenProvider
  ) {
    this.options = options;
    this.graphqlClient = graphqlClient;
    this.tokenProvider = tokenProvider;
  }

  /**
   * @name set
   * @name_zh 
   * @description 
   *
   * @param {UdfTargetType} targetType  USER ROLE 
   * @param {string} key  key
   * @param {UdfDataType} dataType STRING NUMBER DATETIME BOOLEAN  boolean OBJECT 
   * @param {string} label  Label Human Readable 
   *
   * @example
   *
   * import { ManagementClient, UdfTargetType, UdfDataType  } from "authing-js-sdk"
   * const udf = await managementClient.udf.set(
   *    UdfTargetType.User,
   *    'school',
   *    UdfDataType.String,
   *    ''
   * );
   *
   * @example
   *
   * //  age 
   *
   * import { ManagementClient, UdfTargetType, UdfDataType  } from "authing-js-sdk"
   * const udf = await managementClient.udf.set(
   *    UdfTargetType.User,
   *    'age',
   *    UdfDataType.Number,
   *    ''
   * );
   *
   * //  age 
   *
   * const udf = await managementClient.udf.set(
   *    UdfTargetType.User,
   *    'age',
   *    UdfDataType.Number,
   *    ''
   * );
   *
   * @returns {Promise<UserDefinedField[]>}
   * @memberof UdfManagementClient
   */
  async set(
    targetType: UdfTargetType,
    key: string,
    dataType: UdfDataType,
    label: string
  ): Promise<UserDefinedField> {
    const { setUdf: data } = await setUdf(
      this.graphqlClient,
      this.tokenProvider,
      {
        targetType,
        dataType,
        key,
        label
      }
    );
    return data;
  }

  /**
   * @name remove
   * @name_zh 
   * @description 
   *
   * @param {UdfTargetType} targetType  USER ROLE 
   * @param {string} key  key
   *
   * @example
   *
   * await managementClient.udf.remove(UdfTargetType.User, 'school');
   *
   * @returns {Promise<UserDefinedField[]>}
   * @memberof UdfManagementClient
   */
  async remove(targetType: UdfTargetType, key: string): Promise<CommonMessage> {
    const { removeUdf: list } = await removeUdf(
      this.graphqlClient,
      this.tokenProvider,
      {
        targetType,
        key
      }
    );
    return list;
  }

  /**
   * @name list
   * @name_zh 
   * @description 
   *
   * @param {UdfTargetType} targetType  USER ROLE 
   * @example
   *
   * const list = await managementClient.udf.list(UdfTargetType.User);
   *
   * @returns {Promise<UserDefinedField[]>}
   * @memberof UdfManagementClient
   */
  async list(targetType: UdfTargetType): Promise<UserDefinedField[]> {
    const { udf: list } = await udf(this.graphqlClient, this.tokenProvider, {
      targetType
    });
    return list;
  }

  /**
   * @name listUdv
   * @name_zh 
   * @description 
   *
   *
   * @param {UdfTargetType} targetType  USER ROLE 
   * @param {string} targetId  id ID
   * @example
   *
   * udfManagementClient.listUdv('USER', 'userId')
   *
   * @returns {Promise<Array<UserDefinedData>>}
   * @memberof UdfManagementClient
   */
  async listUdv(
    targetType: UdfTargetType,
    targetId: string
  ): Promise<Array<UserDefinedData>> {
    const { udv: list } = await udv(this.graphqlClient, this.tokenProvider, {
      targetType,
      targetId
    });
    return convertUdv(list);
  }

  /**
   * @name setUdvBatch
   * @name_zh 
   * @description 
   *
   * @param {UdfTargetType} targetType  USER ROLE 
   * @param {string} targetId  id ID
   * @param {Object[]} [udvList]
   * @param {boolean} [udvList.key]  key
   * @param {string} [udvList.value] 
   *
   * @example
   *
   * udfManagementClient.setUdv('USER', 'userId', [{key: 'school', 'value': ''}])
   *
   * @returns {Promise<Array<UserDefinedData>>}
   * @memberof UdfManagementClient
   */
  async setUdvBatch(
    targetType: UdfTargetType,
    targetId: string,
    udvList: {
      key: string;
      value: any;
    }[]
  ): Promise<Array<UserDefinedData>> {
    const { setUdvBatch: list } = await setUdvBatch(
      this.graphqlClient,
      this.tokenProvider,
      {
        targetType,
        targetId,
        udvList: udvList.map(item => ({
          key: item.key,
          value: JSON.stringify(item.value)
        }))
      }
    );
    return convertUdv(list);
  }
}
