import { GraphqlClient } from './../common/GraphqlClient';
import { ManagementTokenProvider } from './ManagementTokenProvider';
import { ManagementClientOptions } from './types';
import {
  PaginatedPolicies,
  PolicyAssignmentTargetType,
  PolicyStatement,
  Policy,
  CommonMessage,
  PaginatedPolicyAssignments
} from '../../types/graphql.v2';
import {
  policies,
  policy,
  createPolicy,
  updatePolicy,
  deletePolicy,
  deletePolicies,
  policyAssignments,
  addPolicyAssignments,
  removePolicyAssignments,
  enablePolicyAssignment,
  disablePolicyAssignment
} from '../graphqlapi';
import { DeepPartial } from '../../types/index';

/**
 * @class PoliciesManagementClient 
 * @name PoliciesManagementClient
 * @description Authing **Resource****Policy**
 *
 *  https://docs.authing.co/docs/access-control/index.html
 *
 * @example
 *
 * 
 * \`\`\`javascript
 * import { ManagementClient } from "authing-js-sdk"
 * const managementClient = new ManagementClient({
 *    userPoolId: "YOUR_USERPOOL_ID",
 *    secret: "YOUR_USERPOOL_SECRET",
 * })
 * managementClient.policies.list // 
 * managementClient.policies.create // 
 * managementClient.policies.listUsers // 
 * \`\`\`
 *
 */
export class PoliciesManagementClient {
  options: ManagementClientOptions;
  graphqlClient: GraphqlClient;
  tokenProvider: ManagementTokenProvider;

  constructor(
    options: ManagementClientOptions,
    graphqlClient: GraphqlClient,
    tokenProvider: ManagementTokenProvider
  ) {
    this.options = options;
    this.graphqlClient = graphqlClient;
    this.tokenProvider = tokenProvider;
  }
  /**
   * @name create
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   * @param {PolicyStatement[]}  https://docs.authing.co/docs/access-control/index.html
   * @param {string} [description] 
   *
   * @example
   *
   * import { PolicyEffect } from "authing-js-sdk"
   *
   * const statements = [
   *   {
   *     resource: 'books:123',
   *     effect: PolicyEffect.Allow,
   *     actions: ['books:edit']
   *   }
   * ];
   *
   * const policy = await managementClient.policies.create(code, statements);
   *
   * @returns {Promise<DeepPartial<Policy>>}
   * @memberof PoliciesManagementClient
   */
  async create({
    code,
    statements,
    description,
    namespace
  }: {
    code: string;
    statements: PolicyStatement[];
    description?: string;
    namespace?: string;
  }): Promise<DeepPartial<Policy>> {
    const res = await createPolicy(this.graphqlClient, this.tokenProvider, {
      code,
      statements,
      description,
      namespace
    });
    return res.createPolicy;
  }

  /**
   * @name delete
   * @name_zh 
   * @description  Authing 
   *
   * @example
   *
   * const { code, message } = await managementClient.policies.delete("CODE"); //  code  200 
   *
   *
   * @param {string} code 
   * @returns {Promise<CommonMessage>}
   * @memberof PoliciesManagementClient
   *
   */
  async delete(code: string, namespace?: string): Promise<CommonMessage> {
    const { deletePolicy: data } = await deletePolicy(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        namespace
      }
    );
    return data;
  }

  /**
   * @name deleteMany
   * @name_zh 
   * @description  Authing 
   *
   * @example
   *
   * const { code, message } = await managementClient.policies.deleteMany(["CODE"]); //  code  200 
   *
   *
   * @param {string} codeList 
   * @returns {Promise<CommonMessage>}
   * @memberof PoliciesManagementClient
   *
   */
  async deleteMany(
    codeList: string[],
    namespace?: string
  ): Promise<CommonMessage> {
    const { deletePolicies: data } = await deletePolicies(
      this.graphqlClient,
      this.tokenProvider,
      {
        codeList,
        namespace
      }
    );
    return data;
  }

  /**
   * @name update
   * @name_zh 
   * @description  Authing 
   *
   * @param {string} code 
   * @param {Object} updates
   * @param {string} [updates.description] 
   * @param {PolicyStatement[]} [updates.statements]  https://docs.authing.co/docs/access-control/index.html
   * @param {string} [updates.newCode] 
   *
   * @example
   *
   * const policy = await managementClient.policies.update('CODE', { newCode: 'NEWCODE' });
   *
   * @returns {Promise<DeepPartial<Policy>>}
   * @memberof PoliciesManagementClient
   *
   */
  async update(
    code: string,
    updates: {
      statements?: PolicyStatement[];
      description?: string;
      newCode?: string;
      namespace?: string;
    }
  ): Promise<DeepPartial<Policy>> {
    const { description, statements, newCode, namespace } = updates;
    const { updatePolicy: data } = await updatePolicy(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        description,
        statements,
        newCode,
        namespace
      }
    );
    return data;
  }

  /**
   * @name detail
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   *
   * const policy = await managementClient.policies.detail('CODE');
   *
   * @returns {Promise<DeepPartial<Policy>>}
   * @memberof PoliciesManagementClient
   */
  async detail(code: string, namespace?: string): Promise<DeepPartial<Policy>> {
    const { policy: data } = await policy(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        namespace
      }
    );
    return data;
  }

  /**
   * @name list
   * @name_zh 
   * @description 
   *
   * @param {Object} options
   * @param {number} [options.page=1]
   * @param {number} [options.limit=10]
   * @param {number} [options.namespace='default'] 
   * @param {boolean} [options.excludeDefault=true] 
   *
   * @example
   *
   * const { list, totalCount } = await managementClient.policies.list({
   *   excludeDefault: false // 
   * });
   *
   * @returns {Promise<DeepPartial<PaginatedPolicies>>}
   * @memberof PoliciesManagementClient
   */
  async list(options?: {
    page?: number;
    limit?: number;
    namespace?: string;
  }): Promise<DeepPartial<PaginatedPolicies>> {
    options = options || {};
    const { page = 1, limit = 10, namespace } = options;
    const { policies: data } = await policies(
      this.graphqlClient,
      this.tokenProvider,
      {
        page,
        limit,
        namespace
      }
    );
    return data;
  }

  /**
   * @name listAssignments
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   * @param {number} [page=1]
   * @param {number} [limit=10]
   *
   * @example
   *
   * const { totalCount, list } = await managementClient.policies.listAssignments("CODE");
   *
   * // list 
   *
   *[
   *  {
   *    code: "PolicyCode", // 
   *    targetType: 'USER', // 'USER' , 'ROLE' 
   *    targetIdentifier: '5f8812866795cc0026352fc5' //  ID  code
   *  },
   *  {
   *    code: "PolicyCode", // 
   *    targetType: 'ROLE', // 'USER' , 'ROLE' 
   *    targetIdentifier: 'ROLE_CODE' //  ID  code
   *  }
   *]
   *
   * @returns {Promise<PaginatedPolicyAssignments>}
   * @memberof PoliciesManagementClient
   */
  async listAssignments({
    code,
    namespace,
    page = 1,
    limit = 10
  }: {
    code: string;
    namespace?: string;
    page?: number;
    limit?: number;
  }): Promise<PaginatedPolicyAssignments> {
    const { policyAssignments: data } = await policyAssignments(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        page,
        limit,
        namespace
      }
    );
    return data;
  }

  /**
   * @name addAssignments
   * @name_zh 
   * @description  
   *
   * @param {string[]} policies  code 
   * @param {PolicyAssignmentTargetType} targetType  USER ()  ROLE ()
   * @param {string[]} targetIdentifiers  id  code 
   *
   * @example
   *
   * import { PolicyAssignmentTargetType } from "authing-js-sdk"
   *
   * await managementClient.policies.addAssignments(
   *   ["code1", "code2"],
   *   PolicyAssignmentTargetType.User,
   *   ['USERID']
   * );
   *
   * await managementClient.policies.addAssignments(
   *   ["code1", "code2"],
   *   PolicyAssignmentTargetType.Role,
   *   ['ROLE_CODE']
   * );
   *
   * @returns {Promise<CommonMessage>}
   * @memberof PoliciesManagementClient
   */
  async addAssignments(
    policies: string[],
    targetType: PolicyAssignmentTargetType,
    targetIdentifiers: string[],
    options?: {
      inheritByChildren?: boolean;
      namespace?: string;
    }
  ): Promise<CommonMessage> {
    const { inheritByChildren, namespace } = options || {};
    const res = await addPolicyAssignments(
      this.graphqlClient,
      this.tokenProvider,
      {
        policies,
        targetType,
        targetIdentifiers,
        inheritByChildren,
        namespace
      }
    );
    return res.addPolicyAssignments;
  }

  /**
   * @name removeAssignments
   * @name_zh 
   * @description 
   *
   * @param {string[]} policies  code 
   * @param {PolicyAssignmentTargetType} targetType  USER ()  ROLE ()
   * @param {string[]} targetIdentifiers  id  code 
   *
   * @example
   *
   * import { PolicyAssignmentTargetType } from "authing-js-sdk"
   *
   * await managementClient.policies.removeAssignments(
   *   ["code1", "code2"],
   *   PolicyAssignmentTargetType.User,
   *   ['USERID']
   * );
   *
   * await managementClient.policies.removeAssignments(
   *   ["code1", "code2"],
   *   PolicyAssignmentTargetType.Role,
   *   ['ROLE_CODE']
   * );
   *
   * @returns {Promise<CommonMessage>}
   * @memberof PoliciesManagementClient
   */
  async removeAssignments(
    policies: string[],
    targetType: PolicyAssignmentTargetType,
    targetIdentifiers: string[],
    namespace?: string
  ): Promise<CommonMessage> {
    const res = await removePolicyAssignments(
      this.graphqlClient,
      this.tokenProvider,
      {
        policies,
        targetType,
        targetIdentifiers,
        namespace
      }
    );
    return res.removePolicyAssignments;
  }

  /**
   * @name enableAssignment
   * @name_zh 
   * @description 
   *
   * @param {string} policy  code
   * @param {PolicyAssignmentTargetType} targetType  USER (), ROLE (), GROUP, ORG
   * @param {string} targetIdentifier  id  code code ID
   *
   * @example
   *
   * import { PolicyAssignmentTargetType } from "authing-js-sdk"
   *
   * await managementClient.policies.enableAssignment(
   *   "code1",
   *   PolicyAssignmentTargetType.User,
   *   'USERID'
   * );
   *
   * @returns {Promise<CommonMessage>}
   * @memberof PoliciesManagementClient
   */
  async enableAssignment(
    policy: string,
    targetType: PolicyAssignmentTargetType,
    targetIdentifier: string,
    namespace?: string
  ): Promise<CommonMessage> {
    const res = await enablePolicyAssignment(
      this.graphqlClient,
      this.tokenProvider,
      {
        policy,
        targetType,
        targetIdentifier,
        namespace
      }
    );
    return res.enablePolicyAssignment;
  }

  /**
   * @name disableAssignment
   * @name_zh 
   * @description 
   *
   * @param {string} policy  code
   * @param {PolicyAssignmentTargetType} targetType  USER (), ROLE (), GROUP, ORG
   * @param {string} targetIdentifier  id  code code ID
   *
   * @example
   *
   * import { PolicyAssignmentTargetType } from "authing-js-sdk"
   *
   * await managementClient.policies.disableAssignment(
   *   "code1",
   *   PolicyAssignmentTargetType.User,
   *   'USERID'
   * );
   *
   * @returns {Promise<CommonMessage>}
   * @memberof PoliciesManagementClient
   */
  async disableAssignment(
    policy: string,
    targetType: PolicyAssignmentTargetType,
    targetIdentifier: string,
    namespace?: string
  ): Promise<CommonMessage> {
    const res = await disablePolicyAssignment(
      this.graphqlClient,
      this.tokenProvider,
      {
        policy,
        targetType,
        targetIdentifier,
        namespace
      }
    );
    return res.disbalePolicyAssignment;
  }
}
