import { AuthenticationTokenProvider } from './AuthenticationTokenProvider';
import { AuthenticationClientOptions, RelayMethodEnum } from './types';
import { HttpClient } from '../common/HttpClient';
import {
  serialize,
  popupCenter,
  isMobileBrowser,
  generateRandomString
} from '../utils';
import { Identity, User } from '../../types/index';
import { BaseAuthenticationClient } from './BaseAuthenticationClient';

/**
 * @class SocialAuthenticationClient 
 * @description  API 
 *
 * 
 *
 * \`\`\`javascript
 * import { AuthenticationClient } from "authing-js-sdk"
 * const authenticationClient = new AuthenticationClient({
 *    appId: "YOUR_APP_ID",
 * })
 * await authenticationClient.social.authorize("github", {
 *    onSuccess: (user) => { console.log(user) },
 *    onError: (code, message) => {  }
 * })
 * \`\`\`
 *
 *
 * @name SocialAuthenticationClient
 */
export class SocialAuthenticationClient {
  options: AuthenticationClientOptions;
  tokenProvider: AuthenticationTokenProvider;
  httpClient: HttpClient;
  baseClient: BaseAuthenticationClient;

  constructor(
    options: AuthenticationClientOptions,
    tokenProvider: AuthenticationTokenProvider,
    httpClient: HttpClient
  ) {
    this.options = options;
    this.tokenProvider = tokenProvider;
    this.httpClient = httpClient;
    this.baseClient = new BaseAuthenticationClient(options);
  }

  /**
   * @name authorize
   * @name_zh 
   * @description  GitHub
   *  onSuccess 
   *
   * @param {string} provider 
   * @param {object} [options]
   * @param {boolean} [options.popup=true]  false window.open   tab 
   * @param {Function} [options.onSuccess] 
   * @param {Function} [options.onError]  code  message [Authing ](https://docs.authing.co/advanced/error-code.html)
   * @param {object} [options.position]  options.popup  ture  { w: 585, h: 649 } 
   * @param {object} [options.authorizationParams] 
   *
   * @example
   *
   * //  GitHub 
   *
   * const authenticationClient = new AuthenticationClient({
   *    appId: "YOUR_APP_ID",
   * })
   *
   * await authenticationClient.social.authorize("github", {
   *    onSuccess: (user) => { console.log(user) },
   *    onError: (code, message) => {  },
   *    // 
   *    position: {
   *      w: 100,
   *      h: 100
   *    }
   * })
   *
   * @example
   *
   * //  tab 
   *
   * const authenticationClient = new AuthenticationClient({
   *    appId: "YOUR_APP_ID",
   * })
   *
   * await authenticationClient.social.authorize("github", {
   *    popup: false,
   *    onSuccess: (user) => { console.log(user) },
   *    onError: (code, message) => {  },
   * })
   *
   * @memberof SocialAuthenticationClient
   */
  authorize(
    provider: string,
    options?: {
      popup?: boolean;
      onSuccess?: (user: User) => void;
      onError?: (code: number, message: string, data: any) => void;
      position?: {
        w: number;
        h: number;
      };
      authorization_params?: Record<string, any>; // 
      authorizationParams?: Record<string, any>;
      context?: { [x: string]: any };
      /**
       * @description 
       */
      customData?: { [x: string]: any };

      /**
       * @description  identities
       */
      withIdentities?: boolean;

      /**
       * @description 
       */
      withCustomData?: boolean;

      /**
       * @description 
       */
      protocol?: string;
      uuid?: string;

      // social v3 
      targetUrl?: string;
      relayMethod?: RelayMethodEnum;
      //  guard 
      guardVersion?: string;
    }
  ) {
    options = options || {};
    let {
      position,
      popup = true,
      onSuccess,
      onError,
      authorization_params,
      authorizationParams,
      context,
      customData,
      withIdentities = false,
      protocol = 'oidc',
      uuid,
      withCustomData = false,
      targetUrl,
      relayMethod,
      guardVersion
    } = options;

    if (!uuid) {
      uuid = generateRandomString(20);
    }

    const query: Record<string, string> = {
      from_guard: '1',
      app_id: this.options.appId,
      authorization_params: JSON.stringify(
        authorization_params || authorizationParams
      ),
      with_identities: withIdentities ? '1' : '0',
      with_custom_data: withCustomData ? '1' : '0',
      protocol,
      uuid,
      target_url: targetUrl,
      relay_method: relayMethod,
      guard_version: guardVersion
    };
    if (context) {
      query.context = JSON.stringify(context);
    }
    if (this.options.tenantId) {
      query.tenant_id = this.options.tenantId;
    }
    if (customData) {
      query.custom_data = JSON.stringify(customData);
    }
    let url = `${
      this.baseClient.appHost
    }/connections/social/${provider}?${serialize(query)}`;

    const onMessage = (e: MessageEvent) => {
      let { code, message, data: resData, event } = e.data;
      event = event || {};
      const { source, eventType } = event;
      if (source !== 'authing' || eventType !== 'socialLogin') {
        return;
      }

      try {
        const parsedMsg = JSON.parse(message);
        code = parsedMsg.code;
      } catch (e) {
        // do nothing...
      }
      if (code === 200) {
        //  token
        this.tokenProvider.setUser(resData);
        onSuccess && onSuccess(resData);
      } else {
        onError && onError(code, message, resData);
      }
      window.removeEventListener('message', onMessage);
    };
    window.addEventListener('message', onMessage);

    if (isMobileBrowser()) {
      url = `${url}&redirected=1&from_hosted_guard=1&redirect_url=${this.baseClient.appHost}/interaction/oidc/${uuid}/login`;
      // 
      window.location.href = url;
    } else if (popup) {
      popupCenter(url, position);
    } else {
      window.open(url);
    }

    return Promise.resolve();
  }

  /**
   * 
   */
  public async generateLinkExtIdpUrl(params: {
    extIdpConnIdentifier: string;
    idToken: string;
  }): Promise<{url: string}> {
    const { extIdpConnIdentifier, idToken } = params;
    const data = await this.httpClient.request({
      method: 'GET',
      url: `${this.baseClient.appHost}/api/v3/generate-link-extidp-url`,
      params: {
        ext_idp_conn_identifier: extIdpConnIdentifier,
        id_token: idToken,
        app_id: this.options.appId
      }
    });
    return data;
  }

  /**
   *  ID ID
   */
  public async unlinkExtIdp(params: { extIdpId: string }) {
    const { extIdpId } = params;
    const data = await this.httpClient.request({
      method: 'POST',
      url: `${this.baseClient.appHost}/api/v3/unlink-extidp`,
      data: {
        extIdpId
      }
    });
    return data;
  }

  public async getIdentities(): Promise<Identity[]> {
    const data = await this.httpClient.request({
      method: 'GET',
      url: `${this.baseClient.appHost}/api/v3/get-identities`
    });
    return data;
  }
}
