import { GraphqlClient } from './../common/GraphqlClient';
import { ManagementTokenProvider } from './ManagementTokenProvider';
import { ManagementClientOptions } from './types';
import {
  addUserToGroup,
  createGroup,
  deleteGroups,
  getGroups,
  group,
  groupWithUsers,
  groupWithUsersWithCustomData,
  listGroupAuthorizedResources,
  removeUserFromGroup,
  updateGroup
} from '../graphqlapi';
import {
  CommonMessage,
  Group,
  PaginatedAuthorizedResources,
  PaginatedGroups,
  ResourceType
} from '../../types/graphql.v2';
import { DeepPartial, PaginatedUsers } from '../../types/index';
import { convertUdvToKeyValuePair, formatAuthorizedResources } from '../utils';

/**
 * @class GroupsManagementClient 
 * @description  Authing // 
 *
 * @example
 *
 * 
 * \`\`\`javascript
 * import { ManagementClient } from "authing-js-sdk"
 * const managementClient = new ManagementClient({
 *    userPoolId: "YOUR_USERPOOL_ID",
 *    secret: "YOUR_USERPOOL_SECRET",
 * })
 * managementClient.groups.list // 
 * managementClient.groups.create // 
 * managementClient.groups.listUsers // 
 * \`\`\`
 *
 * @name GroupsManagementClient
 */
export class GroupsManagementClient {
  options: ManagementClientOptions;
  graphqlClient: GraphqlClient;
  tokenProvider: ManagementTokenProvider;

  constructor(
    options: ManagementClientOptions,
    graphqlClient: GraphqlClient,
    tokenProvider: ManagementTokenProvider
  ) {
    this.options = options;
    this.graphqlClient = graphqlClient;
    this.tokenProvider = tokenProvider;
  }

  /**
   * @name create
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   * @param {string} name 
   * @param {string} [description] 
   *
   * @example
   * managementClient.groups.create('group', ' xxx')
   *
   * @returns {Promise<DeepPartial<Group>>}
   * @memberof GroupsManagementClient
   */
  async create(
    code: string,
    name: string,
    description?: string
  ): Promise<DeepPartial<Group>> {
    const res = await createGroup(this.graphqlClient, this.tokenProvider, {
      code,
      name,
      description
    });
    return res.createGroup;
  }

  /**
   * @name delete
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   *
   * @example
   * managementClient.groups.delete('rolea')
   *
   * @returns {Promise<CommonMessage>}
   * @memberof GroupsManagementClient
   */
  async delete(code: string): Promise<CommonMessage> {
    const { deleteGroups: data } = await deleteGroups(
      this.graphqlClient,
      this.tokenProvider,
      {
        codeList: [code]
      }
    );
    return data;
  }

  /**
   * @name update
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   * @param {Object} input
   * @param {string} [input.name] 
   * @param {string} [input.description] 
   * @param {string} [input.newCode] 
   *
   * @example
   * managementClient.groups.update('group', {newCode: 'newcode'})
   *
   *
   * @returns {Promise<DeepPartial<Group>>}
   * @memberof GroupsManagementClient
   */
  async update(
    code: string,
    input: {
      description?: string;
      newCode?: string;
      name?: string;
    }
  ): Promise<DeepPartial<Group>> {
    const { description, newCode, name } = input;
    const { updateGroup: data } = await updateGroup(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        name,
        description,
        newCode
      }
    );
    return data;
  }

  /**
   * @name detail
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   *
   * @example
   * managementClient.groups.detail('manager')
   *
   * @returns {Promise<DeepPartial<Group>>} 
   * @memberof GroupsManagementClient
   */
  async detail(code: string): Promise<DeepPartial<Group>> {
    const { group: data } = await group(
      this.graphqlClient,
      this.tokenProvider,
      {
        code
      }
    );
    return data;
  }

  /**
   * @name list
   * @name_zh 
   * @description 
   *
   * @param {number} [page=1] 
   * @param {number} [limit=10] 
   *
   * @example
   *
   * managementClient.groups.list(1, 10)
   *
   * @returns {Promise<DeepPartial<PaginatedGroups>>}
   * @memberof GroupsManagementClient
   */
  async list(
    page: number = 1,
    limit: number = 10
  ): Promise<DeepPartial<PaginatedGroups>> {
    const { groups: data } = await getGroups(
      this.graphqlClient,
      this.tokenProvider,
      {
        page,
        limit
      }
    );
    return data;
  }

  /**
   * @name deleteMany
   * @name_zh 
   * @description 
   *
   * @param {string[]} codeList 
   *
   * @example
   * managementClient.groups.deleteMany(['groupa', 'groupb'])
   *
   * @returns {Promise<CommonMessage>}
   * @memberof GroupsManagementClient
   */
  async deleteMany(codeList: string[]): Promise<CommonMessage> {
    const { deleteGroups: data } = await deleteGroups(
      this.graphqlClient,
      this.tokenProvider,
      {
        codeList
      }
    );
    return data;
  }

  /**
   * @name listUsers
   * @name_zh 
   * @description 
   * @param {string} code 
   * @param {number} [page=1] 
   * @param {number} [limit=10] 
   *
   * @example
   *
   * managementClient.groups.listUsers(code)
   *
   * @returns {Promise<DeepPartial<PaginatedUsers>>}
   * @memberof GroupsManagementClient
   */
  async listUsers(
    code: string,
    options?: {
      withCustomData?: boolean;
      page?: number;
      limit?: number;
    }
  ): Promise<DeepPartial<PaginatedUsers>> {
    const { withCustomData = false, page = 1, limit = 10 } = options || {};
    if (!withCustomData) {
      const { group: data } = await groupWithUsers(
        this.graphqlClient,
        this.tokenProvider,
        {
          code,
          page,
          limit
        }
      );
      return data.users;
    } else {
      const { group: data } = await groupWithUsersWithCustomData(
        this.graphqlClient,
        this.tokenProvider,
        {
          code,
          page,
          limit
        }
      );
      let { totalCount, list } = data.users;
      list = list.map(user => {
        // @ts-ignore
        user.customData = convertUdvToKeyValuePair(user.customData);
        return user;
      });
      return {
        totalCount,
        // @ts-ignore
        list
      };
    }
  }

  /**
   * @name addUsers
   * @name_zh 
   * @description 
   *
   * @param {string} code 
   * @param {string[]} userIds  ID 
   *
   * @example
   * managementClient.groups.addUsers(code, ['USERID1', 'USERID2'])
   *
   * @returns {Promise<CommonMessage>}
   * @memberof GroupsManagementClient
   */
  async addUsers(code: string, userIds: string[]): Promise<CommonMessage> {
    const res = await addUserToGroup(this.graphqlClient, this.tokenProvider, {
      code,
      userIds
    });
    return res.addUserToGroup;
  }

  /**
   * @name removeUsers
   * @name_zh 
   *
   * @description 
   *
   * @param {string} code 
   * @param {string[]} userIds  ID 
   * @example
   * managementClient.groups.removeUsers(code, ['USERID1', 'USERID2'])
   *
   * @returns {Promise<CommonMessage>}
   * @memberof GroupsManagementClient
   */
  async removeUsers(code: string, userIds: string[]): Promise<CommonMessage> {
    const res = await removeUserFromGroup(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        userIds
      }
    );
    return res.removeUserFromGroup;
  }

  /**
   * @description 
   *
   * @param code:  code
   * @param namespace:  namespace code
   * @param options.resourceType 
   */
  public async listAuthorizedResources(
    code: string,
    namespace: string,
    options?: {
      resourceType?: ResourceType;
    }
  ): Promise<PaginatedAuthorizedResources> {
    const { resourceType } = options || {};
    const { group } = await listGroupAuthorizedResources(
      this.graphqlClient,
      this.tokenProvider,
      {
        code,
        namespace,
        resourceType
      }
    );
    if (!group) {
      throw new Error('');
    }
    let {
      authorizedResources: { list, totalCount }
    } = group;
    list = formatAuthorizedResources(list);
    return {
      list,
      totalCount
    };
  }
}
