/**
 *  AuthenticationClientOptions 
 */
export interface AuthenticationClientOptions {
  /**  ID */
  appId: string;
  /**  ID */
  tenantId?: string;
  /**  https://sample-app.authing.cn '/' */
  appHost?: string;
  /**  */
  secret?: string;
  /**  */
  protocol?: 'oauth' | 'oidc' | 'saml' | 'cas';
  /**  token  */
  tokenEndPointAuthMethod?:
    | 'client_secret_post'
    | 'client_secret_basic'
    | 'none';
  /**  token  */
  introspectionEndPointAuthMethod?:
    | 'client_secret_post'
    | 'client_secret_basic'
    | 'none';
  /**  token  */
  revocationEndPointAuthMethod?:
    | 'client_secret_post'
    | 'client_secret_basic'
    | 'none';
  /**  */
  redirectUri?: string;
  /**  **/
  timeout?: number;
  /** ,  (err: Error) => { throw err }  **/
  onError?: (code: number, message: string, data?: any) => void;
  /** Websocket  */
  websocketHost?: string;
  /**  */
  requestFrom?: string;
  /** token */
  token?: string;
  /**  */
  encryptFunction?: (
    plainText: string,
    publicKey: string,
    encryption?: Encryption
  ) => Promise<string>;
  /**  */
  publicKey?: string;
  httpClient?: typeof HttpClient;
  graphqlClient?: typeof GraphqlClient;
  tokenProvider?: typeof AuthenticationTokenProvider;
  /**  Token  */
  privateKeys?: PrivateKey[];
  /**
   * 
   */
  lang?: Lang;

  /**
   * @deprecated  appHost
   */
  host?: string;
  /**
   * @deprecated  appHost
   */
  domain?: string;

  /**
   *  key Authing 
   */
  headers?: {
    'userpool-id': string;
    'app-id': string;
    'tenant-id'?: string;
    'sdk-version': string;
    'request-from': string;
    lang: string;
  };
  encryption?: Encryption;

  //  socket 
  socketUri?: string;

  // 
  retryTimes?: number;
}

