import {
  ManagementClientOptions,
  AgreementInput,
  AgreementDetail,
  AgreementList
} from './types';
import { GraphqlClient } from '../common/GraphqlClient';
import { HttpClient } from '../common/HttpClient';
import { ManagementTokenProvider } from './ManagementTokenProvider';

/**
 * @class AgreementManagementClient 
 * @description 
 *
 * @example
 *
 * 
 * \`\`\`javascript
 * import { ManagementClient } from "authing-js-sdk"
 * const managementClient = new ManagementClient({
 *    userPoolId: "YOUR_USERPOOL_ID",
 *    secret: "YOUR_USERPOOL_SECRET",
 * })
 * managementClient.applications.listAgreement("APP_ID") // 
 * \`\`\`
 *
 * @name AgreementManagementClient
 */
export class AgreementManagementClient {
  options: ManagementClientOptions;
  graphqlClient: GraphqlClient;
  tokenProvider: ManagementTokenProvider;
  httpClient: HttpClient;

  constructor(
    options: ManagementClientOptions,
    graphqlClient: GraphqlClient,
    httpClient: HttpClient,
    tokenProvider: ManagementTokenProvider
  ) {
    this.options = options;
    this.httpClient = httpClient;
    this.graphqlClient = graphqlClient;
    this.tokenProvider = tokenProvider;
  }

  /**
   * @description 
   * @param {object} appId  ID
   * @param {object} agreement
   * @param {string} agreement.title  HTML A 
   * @param {boolean} [agreement.required]  true
   * @param {lang} [agreement.lang]  zh-CN, en-US zh-CN
   */
  public async create(
    appId: string,
    agreement: AgreementInput
  ): Promise<AgreementDetail> {
    const mergedData: AgreementInput = {
      lang: 'zh-CN',
      required: true,
      ...agreement
    };

    const data = await this.httpClient.request({
      url: `${this.options.host}/api/v2/applications/${appId}/agreements`,
      method: 'POST',
      data: mergedData
    });

    return data;
  }

  /**
   * @description 
   * @param {string} appId  ID
   */
  public async list(appId: string): Promise<AgreementList> {
    const data = await this.httpClient.request({
      url: `${this.options.host}/api/v2/applications/${appId}/agreements`,
      method: 'GET'
    });

    return data;
  }

  /**
   * @description 
   * @param {string} appId  ID
   * @param {number} agreementId  ID
   */
  public async delete(appId: string, agreementId: number): Promise<boolean> {
    await this.httpClient.request({
      url: `${this.options.host}/api/v2/applications/${appId}/agreements/${agreementId}`,
      method: 'DELETE'
    });

    return true;
  }

  /**
   * @description 
   * @param {string} appId  ID
   * @param {number} agreementId  ID
   * @param {object} updates 
   * @param {string} [updates.title]  HTML A 
   * @param {boolean} [updates.required]  true
   * @param {lang} [updates.lang]  zh-CN, en-US zh-CN
   */
  public async modify(
    appId: string,
    agreementId: number,
    updates: AgreementInput
  ): Promise<AgreementDetail> {
    const data = await this.httpClient.request({
      url: `${this.options.host}/api/v2/applications/${appId}/agreements/${agreementId}`,
      method: 'PUT',
      data: updates
    });

    return data;
  }

  /**
   * @description 
   * @param {string} appId  ID
   * @param {number[]} order  ID 
   */
  public async sort(appId: string, order: number[]): Promise<boolean> {
    await this.httpClient.request({
      url: `${this.options.host}/api/v2/applications/${appId}/agreements/sort`,
      method: 'POST',
      data: {
        ids: order
      }
    });

    return true;
  }
}
