    if (options?.redirectUri) {
      return `${this.baseClient.appHost}/oidc/session/end?id_token_hint=${options.idToken}&post_logout_redirect_uri=${options.redirectUri}`;
    }
    return `${this.baseClient.appHost}/oidc/session/end`;
