  /**
   * @name batch
   * @name_zh 
   * @description  IDusernameemailphoneemailexternalId 
   *
   * @param {string[]} identifiers   ID 
   * @param {string} [type]  'id' ,'username' ,'phone' ,'email', 'externalId' 'id'
   *
   * @example
   *
   * const users = await managementClient.users.batch(['USERID'], options);
   *
   * @returns {Promise<CommonMessage>}
   * @memberof UsersManagementClient
   */
  async batch(
    ids: string[],
    options?: {
      queryField: BatchFetchUserTypes;
      withCustomData?: boolean;
    }
  ): Promise<User[]> {
    const { queryField = 'id', withCustomData = false } = options || {};
    const users: User[] = await this.httpClient.request({
      url: `${this.options.host}/api/v2/users/batch`,
      method: 'POST',
      data: {
        ids,
        type: queryField,
        withCustomData
      }
    });
    if (withCustomData) {
      users.map(user => {
        // @ts-ignore
        user.customData = convertUdvToKeyValuePair(user.customData);
        return user;
      });
      return users;
    }
    return users;
  }

  /**
   * @name list
   * @name_zh 
   * @description 
   *
   * @param {number} [page=1] ,  1 
   * @param {number} [limit=10] 
   *
   * @example
   *
   * const user = await managementClient.users.list();
   *
   * @returns
   * @memberof UsersManagementClient
   */
  async list(
    page: number = 1,
    limit: number = 10,
    options?: {
      withCustomData?: boolean;
      excludeUsersInOrg?: boolean;
    }
  ): Promise<PaginatedUsers> {
    const { withCustomData = false, excludeUsersInOrg = false } = options || {};
    if (withCustomData) {
      const { users: data } = await usersWithCustomData(
        this.graphqlClient,
        this.tokenProvider,
        {
          page,
          limit,
          excludeUsersInOrg
        }
      );
      let { totalCount, list } = data;
      list = list.map(user => {
        // @ts-ignore
        user.customData = convertUdvToKeyValuePair(user.customData);
        return user;
      });
      return {
        totalCount,
        list
      };
    } else {
      const { users: data } = await users(
        this.graphqlClient,
        this.tokenProvider,
        {
          page,
          limit,
          excludeUsersInOrg
        }
      );
      return data;
    }
  }

