  /**
   * @name find
   * @name_zh 
   * @description 
   *
   * @param {Object} options
   * @param {string} [options.username] 
   * @param {string} [options.email] 
   * @param {string} [options.phone] 
   * @param {string} [options.externalId] externalId
   *
   * @memberof UsersManagementClient
   */
  async find(options: {
    username?: string;
    email?: string;
    phone?: string;
    externalId?: string;
    withCustomData?: boolean;
    identity?: {
      userIdInIdp: string;
      provider: string;
    };
  }): Promise<User> {
    const {
      username,
      email,
      phone,
      externalId,
      withCustomData = false,
      identity
    } = options;

    if (withCustomData) {
      const { findUser: user } = await findUserWithCustomData(
        this.graphqlClient,
        this.tokenProvider,
        {
          username,
          email,
          phone,
          externalId
        }
      );
      // @ts-ignore
      user.customData = convertUdvToKeyValuePair(user.customData);
      return user;
    } else {
      const { findUser: user } = await findUser(
        this.graphqlClient,
        this.tokenProvider,
        {
          username,
          email,
          phone,
          externalId,
          identity
        }
      );
      return user;
    }
  }

  /**
   * @name search
   * @name_zh 
   * @description 
   *
   * @param query 
   * @param options 
   * @param {string[]} [options.fields]  usernamenicknameemailphonecompanynamegivenNamefamilyNamemiddleNameprofilepreferredUsername 
   *  find 
   * @param {number} [options.page=1]
   * @param {number} [options.limit=10]
   * @param {Object} [options.departmentOpts] 
   * @param {string} [options.departmentOpts.departmentId]  ID
   * @param {string} [options.departmentOpts.includeChildrenDepartments] 
   *
   * @example
   *
   * const { totalCount, list } = await managementClient.users.search("Bob");
   *
   * @returns {Promise<PaginatedUsers>}
   * @memberof UsersManagementClient
   */
  async search(
    query: string,
    options?: {
      fields?: string[];
      page?: number;
      limit?: number;
      //  ID 
      departmentOpts?: QuerySearchUserArgs['departmentOpts'];
      groupOpts?: QuerySearchUserArgs['groupOpts'];
      roleOpts?: QuerySearchUserArgs['roleOpts'];
      withCustomData?: boolean;
    }
  ): Promise<PaginatedUsers> {
    options = options || {};
    const {
      fields,
      page = 1,
      limit = 10,
      departmentOpts,
      groupOpts,
      roleOpts,
      withCustomData = false
    } = options;

    if (!withCustomData) {
      const { searchUser: data } = await searchUser(
        this.graphqlClient,
        this.tokenProvider,
        {
          query,
          fields,
          page,
          limit,
          departmentOpts,
          groupOpts,
          roleOpts
        }
      );
      return data;
    } else {
      const { searchUser: data } = await searchUserWithCustomData(
        this.graphqlClient,
        this.tokenProvider,
        {
          query,
          fields,
          page,
          limit,
          departmentOpts,
          groupOpts,
          roleOpts
        }
      );
      let { totalCount, list } = data;
      list = list.map(user => {
        // @ts-ignore
        user.customData = convertUdvToKeyValuePair(user.customData);
        return user;
      });
      return {
        totalCount,
        list
      };
    }
  }

