  /**
   * @name addRoles
   * @name_zh 
   * @description 
   *
   * @param {string} userId  ID
   * @param {string} roles  code 
   * @param {string} namespace 
   *
   * @example
   *
   * const { code, message } = await managementClient.users.addRoles("USERID", ["ROLEA"]);
   *
   * @returns {Promise<CommonMessage>}
   * @memberof UsersManagementClient
   */
  async addRoles(
    userId: string,
    roles: string[],
    namespace?: string
  ): Promise<CommonMessage> {
    const { assignRole: data } = await assignRole(
      this.graphqlClient,
      this.tokenProvider,
      {
        roleCodes: roles,
        userIds: [userId],
        namespace
      }
    );
    return data;
  }

  /**
   * @name removeRoles
   * @name_zh 
   * @description 
   *
   * @param {string} userId  ID
   * @param {string} roles  code 
   * @param {string} namespace  code
   *
   * @example
   *
   * const { code, message } = await managementClient.users.removeRoles("USERID", ["ROLEA"]);
   *
   * @returns {Promise<CommonMessage>}
   * @memberof UsersManagementClient
   */
  async removeRoles(
    userId: string,
    roles: string[],
    namespace?: string
  ): Promise<CommonMessage> {
    const { revokeRole: data } = await revokeRole(
      this.graphqlClient,
      this.tokenProvider,
      {
        roleCodes: roles,
        userIds: [userId],
        namespace
      }
    );
    return data;
  }

