import { ManagementClientOptions } from './types';
import { GraphqlClient } from '../common/GraphqlClient';
import { ManagementTokenProvider } from './ManagementTokenProvider';
import { updateUserpool } from '../graphqlapi';
import { UpdateUserpoolInput, UserPool } from '../../types/graphql.v2';
import { HttpClient } from '../common/HttpClient';

/**
 * @name UserPoolManagementClient
 * @description Authing 
 *
 *  API 
 *
 * @example
 *
 * 
 * \`\`\`javascript
 * import { ManagementClient } from "authing-js-sdk"
 * const managementClient = new ManagementClient({
 *    userPoolId: "YOUR_USERPOOL_ID",
 *    secret: "YOUR_USERPOOL_SECRET",
 * })
 *
 * managementClient.userpool.detail // 
 * managementClient.userpool.update // 
 * managementClient.userpool.env // 
 * \`\`\`
 *
 * @class UsersManagementClient 
 */
export class UserPoolManagementClient {
  options: ManagementClientOptions;
  graphqlClient: GraphqlClient;
  tokenProvider: ManagementTokenProvider;
  httpClient: HttpClient;

  constructor(
    options: ManagementClientOptions,
    httpClient: HttpClient,
    graphqlClient: GraphqlClient,
    tokenProvider: ManagementTokenProvider
  ) {
    this.options = options;
    this.httpClient = httpClient;
    this.graphqlClient = graphqlClient;
    this.tokenProvider = tokenProvider;
  }

  /**@name detail
   * @name_zh 
   * @description 
   *
   * @example
   *
   * const userpool = await managementClient.userpool.detail()
   *
   * @returns {Promise<UserPool>}
   * @memberof UserPoolManagementClient
   */
  async detail(): Promise<UserPool> {
    const userPool = await this.httpClient.request({
      method: 'GET',
      url: `${this.options.host}/api/v2/userpools/detail`
    });

    return userPool;
  }

  /**
   * @name update
   * @name_zh 
   * @description 
   *
   * @param {UpdateUserpoolInput} updates
   * @param {string} [updates.name] 
   * @param {string} [updates.logo]  logo
   * @param {string} [updates.domain] 
   * @param {string} [updates.description] 
   * @param {boolean} [updates.emailVerifiedDefault]  emailVerified  true
   * @param {boolean} [updates.appSsoEnabled] 
   * @param {boolean} [updates.sendWelcomeEmail] 
   * @param {boolean} [updates.registerDisabled] 
   * @param {string} [updates.allowedOrigins] Allowed Origins  JavaScript  Authing API  URL CORS     URLhttps://*.sample.com
   *  URL 
   * 
   * @param {Object} [updates.whitelist] 
   * @param {boolean} [updates.whitelist.phoneEnabled] 
   * @param {boolean} [updates.whitelist.emailEnabled] 
   * @param {boolean} [updates.whitelist.usernameEnabled] 
   * @param {number} [updates.tokenExpiresAfter] token 
   * @param {Object} [updates.loginFailCheck] 
   * @param {boolean} [updates.loginFailCheck.enabled] 
   * @param {number} [updates.loginFailCheck.timeInterval] 
   * @param {number} [updates.loginFailCheck.limit]  IP 
   * @param {Object} [updates.frequentRegisterCheck]  IP 
   * @param {boolean} [updates.frequentRegisterCheck.enabled] 
   * @param {Object} [updates.frequentRegisterCheck.timeInterval] 
   * @param {Object} [updates.frequentRegisterCheck.limit]  IP 
   *
   *
   * @example
   *
   * const userpool = await managementClient.userpool.update({
   *    registerDisabled: true // 
   * })
   *
   * @returns {Promise<UserPool>}
   * @memberof UserPoolManagementClient
   */
  async update(updates: UpdateUserpoolInput): Promise<UserPool> {
    const res = await updateUserpool(this.graphqlClient, this.tokenProvider, {
      input: updates
    });
    return res.updateUserpool;
  }

  /**
   * @name listEnv
   * @name_zh 
   * @description  pipeline https://docs.authing.co/extensibility/pipeline/env.html
   *
   * @example
   *
   * const envList = await managementClient.userpool.listEnv()
   *
   * @returns {Promise<Env[]>}
   * @memberof UserPoolManagementClient
   */
  async listEnv(): Promise<{ key: string; value: any }[]> {
    return await this.httpClient.request({
      method: 'GET',
      url: `${this.options.host}/api/v2/env`
    });
  }

  /**
   * @name addEnv
   * @name_zh 
   * @description 
   *
   * @param {string} key 
   * @param {any} value 
   *
   * @example
   *
   * const envList = await managementClient.userpool.addEnv('LARK_WEBHOOK', 'xxxxxxx') //  webhook  pipeline : https://docs.authing.co/extensibility/pipeline/usage.html
   *
   * @returns {Promise<Env[]>} 
   * @memberof UserPoolManagementClient
   */
  async addEnv(
    key: string,
    value: any
  ): Promise<{ key: string; value: any }[]> {
    return await this.httpClient.request({
      method: 'POST',
      url: `${this.options.host}/api/v2/env`,
      data: {
        key,
        value
      }
    });
  }

  /**
   * @name removeEnv
   * @name_zh 
   * @description 
   *
   * @param {string} key 
   *
   * @example
   *
   * const envList = await managementClient.userpool.removeEnv('LARK_WEBHOOK')
   *
   * @returns {Promise<Env[]>} 
   * @memberof UserPoolManagementClient
   */
  async removeEnv(key: string): Promise<{ key: string; value: any }[]> {
    return await this.httpClient.request({
      method: 'DELETE',
      url: `${this.options.host}/api/v2/env/${key}`
    });
  }
}
