import { ManagementClientOptions, UserActions } from './types';
import { ManagementTokenProvider } from './ManagementTokenProvider';
import { HttpClient } from '../common/HttpClient';
import { serialize } from '../utils';

/**
 * @class UserActionManagementClient 
 * @name UserActionManagementClient
 * @description 
 *
 * @example
 *
 * 
 * \`\`\`javascript
 * import { UserActionManagementClient } from "authing-js-sdk"
 * const managementClient = new ManagementClient({
 *    userPoolId: "YOUR_USERPOOL_ID",
 *    secret: "YOUR_USERPOOL_SECRET",
 * })
 * managementClient.userAction.list    // 
 * managementClient.userAction.export // 
 * \`\`\`
 *
 */
export class UserActionManagementClient {
  options: ManagementClientOptions;
  httpClient: HttpClient;
  tokenProvider: ManagementTokenProvider;

  constructor(
    options: ManagementClientOptions,
    httpClient: HttpClient,
    tokenProvider: ManagementTokenProvider
  ) {
    this.options = options;
    this.httpClient = httpClient;
    this.tokenProvider = tokenProvider;
  }

  /**
   * 
   * @param options.page 
   * @param options.limit 
   * @param options.clientIp  IP 
   * @param options.operationName 
   * @param options.operatoArn  Arn  searchUser 
   * @returns Promise<UserActions>
   */
  public async list(
    options: {
      page?: number;
      limit?: number;
      clientIp?: string;
      operationName?: string;
      operatoArn?: string;
    } = {
      page: 1,
      limit: 10
    }
  ): Promise<UserActions> {
    const result = await this.httpClient.request({
      method: 'GET',
      url:
        `${this.options.host}/api/v2/analysis/user-action?` +
        serialize({
          page: options.page?.toString(),
          limit: options.limit?.toString(),
          clientip: options.clientIp,
          operation_name: options.operationName,
          operator_arn: options.operatoArn
        })
    });
    return result;
  }
}
